import { Type_RefFuncKrpano } from '@components/Krpano/types';
import { useTourStats } from '@containers/Home/global/TourStats';
import { useKrpano } from '@containers/Home/hook/useKrpano';
import React, { memo } from 'react';

interface Props {
  krpanoRef: Type_RefFuncKrpano | null;
}

const ControlbarDirectionBtn = memo(({ krpanoRef }: Props) => {
  const [tourStats] = useTourStats('stats');

  const { onUp, onDown, onLeft, onRight, onStopVertical, onStopHorizontal } =
    useKrpano({ krpanoRef });

  return (
    <div className="actDirection">
      <div className="actDirectionWrapper">
        <button
          type="button"
          className={`act__item actTop ${
            tourStats.btnDirectionActive === 'top' ? 'active' : ''
          }`}
          onMouseDown={onUp}
          onMouseUp={onStopVertical}
        >
          <span />
        </button>
        <button
          type="button"
          className={`act__item actRight ${
            tourStats.btnDirectionActive === 'right' ? 'active' : ''
          }`}
          onMouseDown={onRight}
          onMouseUp={onStopHorizontal}
        >
          <span />
        </button>
        <button
          type="button"
          className={`act__item actBottom ${
            tourStats.btnDirectionActive === 'bottom' ? 'active' : ''
          }`}
          onMouseDown={onDown}
          onMouseUp={onStopVertical}
        >
          <span />
        </button>
        <button
          type="button"
          className={`act__item actLeft ${
            tourStats.btnDirectionActive === 'left' ? 'active' : ''
          }`}
          onMouseDown={onLeft}
          onMouseUp={onStopHorizontal}
        >
          <span />
        </button>
      </div>
    </div>
  );
});

export default ControlbarDirectionBtn;
